	import {uniCollapse,uniCollapseItem} from "@dcloudio/uni-ui"
	export default {
		data() {
			return {
				array: ['中国', '美国', '巴西', '日本'],
                index: "",
				selectValue:"", 
				imageList:[],
				compony_name:"",
				name:"",
				compony_num:"",
				pathArr:[],
				listPath:[],
				tel:"",
				percent:0,
				showBox:false,
				state:false,
				
			}
		},
		methods: {
			deletePic:function(e){
				var _self = this
				console.log(e)
				_self.imageList.splice(e,1)
				_self.pathArr.splice(e,1)
				console.log(_self.imageList)
			},
			bindPickerChange: function(e) {
            console.log('picker发送选择改变，携带值为', e.target.value)
            this.index = e.target.value
			},
			previewImage: function(e) {
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
		chooseImage: async function() {
			var _self = this
				if (this.imageList.length === 9) {
					let isContinue = await this.isFullImg();
					console.log("是否继续?", isContinue);
					if (!isContinue) {
						return;
					}
				}
				uni.chooseImage({
					success: (res) => {
						console.log(res)
						console.log(JSON.stringify(res.tempFilePaths));
						var tempFilePaths = res.tempFilePaths;
						this.imageList = this.imageList.concat(res.tempFilePaths);
						for(var i =0;i<tempFilePaths.length;i++){
						uni.uploadFile({
							url: _self.$api+'dockingManager/upload', //仅为示例，非真实的接口地址
							//url: "http://www.tp5.com/index", //仅为示例，非真实的接口地址
							filePath: tempFilePaths[i],
							name: 'file', 
							formData: {
								'user': 'test'
							},
							success: (uploadFileRes) => {
								_self.pathArr = _self.pathArr.concat(JSON.parse(uploadFileRes.data))
								_self.state = true 
							}
						}); 
					   }	
					}
				})
			},
			showOrHide:function(){
					this.showBox = false
				},
				isFullImg: function() {
					return new Promise((res) => {
						uni.showModal({
							content: "已经有9张图片了,是否清空现有图片？",
							success: (e) => {
								if (e.confirm) {
									this.imageList = []; 
									res(true);
								} else {
									res(false)
								}
							},
							fail: () => {
								res(false)
							}
						})
					})
				},
				/**
				 * 提交认证
				 */
				addAudit:function(){
					var _self = this
					var myreg = /^((0\d{2,3}-\d{7,8})|(1[3456789]\d{9}))$/;
					if(_self.compony_name == ""){
						uni.showToast({
							title:"企业名称不能为空",
							icon:"none"
						})
						return false
					}else if(_self.name == ""){
						uni.showToast({
							title:"法人姓名不能为空",
							icon:"none"
						})
						return false
					}
					// else if(_self.percent != 100 && _self.imageList.length>0){
					// 		uni.showToast({
					// 			title:"图片正在上传，请稍后提交",
					// 			icon:"none"
					// 		})
					// 		return false
					// 	}
					else if(_self.compony_num == ""){
						uni.showToast({
							title:"企业注册号不能为空",
							icon:"none"
						})
						return false
					}
					else if(_self.tel == ""){
						uni.showToast({
							title:"请输入联系方式",
							icon:"none"
						})
						return false
					}else if(!myreg.test(_self.tel)){
						uni.showToast({
						title:"手机号码格式不正确",
						icon:"none"
					})
					return false
					}
					else if(_self.imageList.length === 0){
						uni.showToast({
							title:"请上传企业照片",
							icon:"none" 
						})
						return false
					}else if(_self.state == false && _self.imageList.length>0){ 
					uni.showToast({
						title:"图片正在上传，请稍后提交",
						icon:"none"  
					})
					return false
					}
					
					for(var i =0;i<_self.pathArr.length;i++){
						_self.listPath.push({testName1:_self.pathArr[i]})
					}
					_self.listPath.push({store:0,nots:_self.compony_num,trade:_self.compony_name,phone:_self.tel,name:_self.name,optionId:uni.getStorageSync("openId")})
					uni.request({
						url:_self.$api+"dockingManager/cardAdd",
						data:JSON.stringify(_self.listPath),
						method:"POST",
						success:function(res){
							console.log(res)   
								if(res.data == 1){ 
								uni.setStorageSync("componyOwner",4)
								uni.showToast({
									title:"提交成功",
									success:function(){
										setTimeout(function(){
											uni.switchTab({ 
												url:"../../../pages/mine/mine"
											})
										},1000)
									}
								})
								return false
							}
							else if(res.data == 99){
								uni.showToast({
									title:"您尚未进行登录，亲登录后重试",
									icon:"none"
								})
								return false
							}
							else if(res.data == 2){
								uni.showToast({
									title:"该企业已注册",
									icon:"none"
								})
								return false
							}
							else if(res.data == 3){
								uni.showToast({
									title:"该企业已在认证阶段",
									icon:"none"
								})
								return false
							}
							else{
								uni.showToast({
									title:"提交失败，请联系管理员",
									icon:"none"
								})
								return false
							}
						}
						})
				}
		},
		 components: {uniCollapse,uniCollapseItem}
	}
